import React, { useState, useEffect } from 'react';
import { followApi } from '../services/followApi';
import { ApiError, ErrorCodes } from '../utils/ApiError';
import './FollowUser.css';

function FollowUser({ followerId, followingId, onFollowChange }) {
  const [isFollowing, setIsFollowing] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [checkingStatus, setCheckingStatus] = useState(true);

  // Kontrollera om anvndaren redan fljs
  useEffect(() => {
    const checkFollowStatus = async () => {
      try {
        setCheckingStatus(true);
        const following = await followApi.getFollowing(followerId);
        const isCurrentlyFollowing = following.some(
          (follow) => follow.followingId === followingId
        );
        setIsFollowing(isCurrentlyFollowing);
      } catch (err) {
        if (err instanceof ApiError) {
          switch (err.errorCode) {
            case ErrorCodes.TOKEN_EXPIRED:
              setError('Din session har gtt ut. Logga in igen.');
              break;
            case ErrorCodes.NETWORK_ERROR:
              setError('Kunde inte ansluta till servern.');
              break;
            case ErrorCodes.TIMEOUT_ERROR:
              setError('Begran tog fr lng tid.');
              break;
            default:
              setError(err.message || 'Kunde inte kontrollera fljstatus');
          }
        } else {
          setError(err.message || 'Kunde inte kontrollera fljstatus');
        }
      } finally {
        setCheckingStatus(false);
      }
    };

    if (followerId && followingId) {
      checkFollowStatus();
    }
  }, [followerId, followingId]);

  const handleFollow = async () => {
    try {
      setLoading(true);
      setError(null);
      await followApi.followUser(followingId);
      setIsFollowing(true);
      if (onFollowChange) {
        onFollowChange(true);
      }
    } catch (err) {
      if (err instanceof ApiError) {
        switch (err.errorCode) {
          case ErrorCodes.TOKEN_EXPIRED:
            setError('Din session har gtt ut. Logga in igen.');
            break;
          case ErrorCodes.NETWORK_ERROR:
            setError('Kunde inte ansluta till servern.');
            break;
          case ErrorCodes.TIMEOUT_ERROR:
            setError('Begran tog fr lng tid.');
            break;
          case ErrorCodes.ALREADY_FOLLOWING:
            setError('Du fljer redan denna anvndare.');
            break;
          case ErrorCodes.INVALID_USER_ID:
            setError('Ogiltigt anvndar-ID.');
            break;
          default:
            setError(err.message || 'Kunde inte flja anvndare');
        }
      } else {
        setError(err.message || 'Kunde inte flja anvndare');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleUnfollow = async () => {
    try {
      setLoading(true);
      setError(null);
      await followApi.unfollowUser(followingId);
      setIsFollowing(false);
      if (onFollowChange) {
        onFollowChange(false);
      }
    } catch (err) {
      if (err instanceof ApiError) {
        switch (err.errorCode) {
          case ErrorCodes.TOKEN_EXPIRED:
            setError('Din session har gtt ut. Logga in igen.');
            break;
          case ErrorCodes.NETWORK_ERROR:
            setError('Kunde inte ansluta till servern.');
            break;
          case ErrorCodes.TIMEOUT_ERROR:
            setError('Begran tog fr lng tid.');
            break;
          case ErrorCodes.USER_NOT_FOUND:
            setError('Anvndaren kunde inte hittas.');
            break;
          default:
            setError(err.message || 'Kunde inte avflja anvndare');
        }
      } else {
        setError(err.message || 'Kunde inte avflja anvndare');
      }
    } finally {
      setLoading(false);
    }
  };

  if (checkingStatus) {
    return <div className="follow-user-loading">Kontrollerar status...</div>;
  }

  return (
    <div className="follow-user">
      {error && <div className="follow-user-error">{error}</div>}
      {isFollowing ? (
        <button
          onClick={handleUnfollow}
          disabled={loading}
          className="follow-user-button unfollow-button"
        >
          {loading ? 'Avfljer...' : 'Avflj'}
        </button>
      ) : (
        <button
          onClick={handleFollow}
          disabled={loading}
          className="follow-user-button follow-button"
        >
          {loading ? 'Fljer...' : 'Flj'}
        </button>
      )}
    </div>
  );
}

export default FollowUser;